html, body { color: #313131; font-family: "interstate", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; font-weight: 400; font-size: 16px; line-height: 1.2; }

img { max-width: 100%; height: auto; }

.wrapper { margin: 0px 1rem; }

@media screen and (min-width: 48rem) { .wrapper { margin: 0px 2rem; } }

@media screen and (min-width: 64rem) { .wrapper { margin: 0px auto; width: 62rem; } }

main section { padding: 4.75rem 0px; }

main section:after { content: ' '; display: block; clear: both; }

main section[data-section="accommodations"] { padding-top: 0px; }

address { font-style: normal; }

address strong { color: #33905a; }

.back-home-wrap { margin: 2rem 0px; text-align: center; }

.back-home-wrap a { color: #000; text-decoration: none; transition: all 250ms; }

.back-home-wrap a span { display: inline-block; }

.back-home-wrap a:hover { color: #0075ff; }

.back-home-wrap a:hover span { transform: translate(-2px, 0px); }

img.img-full { width: 100%; }

hr { margin: 4rem auto; max-width: 600px; background: #000; height: 1px; border: 0px; }

.btn { display: inline-block; padding: 0.625em 1em; color: #fff; background-color: #33905a; -webkit-appearance: none; border: 0px; outline: 0px; border-radius: 0.375em; line-height: 1; font-weight: bold; text-decoration: none; transition: background-color 150ms; will-change: background-color; text-transform: uppercase; }

.btn:hover { cursor: pointer; background-color: #40b672; }

.btn:active { background-color: #62c78c; }

.btn + .btn { margin-left: 1em; }

.btn-sm { font-size: 0.75em; }

.block { display: flex; justify-content: space-between; align-items: center; flex-direction: row; flex-wrap: wrap; flex-flow: row wrap; align-content: flex-end; }

.block:nth-of-type(n+2), .block:not(:first-child) { margin-top: 4rem; }

@media screen and (min-width: 48rem) { .block h2 { text-align: left; } }

.block-images { margin-top: 2rem; }

.block-images img { max-width: 100%; }

.block-content { display: block; }

.block-content h1 { font-size: 1.75rem; text-align: center; }

.block-content h2 { margin: 2rem 0px 0.5rem; font-size: 1.375rem; }

.block-content img { margin: 1rem 0px; width: 100%; }

.block-rsvp, .block-welcome { display: block; }

.block-rsvp .rsvp-button-wrap, .block-welcome .rsvp-button-wrap { text-align: center; }

.block-rsvp .rsvp-fine-print p, .block-welcome .rsvp-fine-print p { font-size: 0.875rem; }

@media screen and (min-width: 48rem) { .block-rsvp, .block-welcome { max-width: 800px; margin: 0px auto; }
  .block-rsvp h2, .block-rsvp h3, .block-welcome h2, .block-welcome h3 { text-align: center; }
  .block-rsvp p, .block-welcome p { font-size: 1.25rem; }
  .block-rsvp .rsvp-fine-print, .block-welcome .rsvp-fine-print { text-align: center; }
  .block-rsvp .rsvp-fine-print p, .block-welcome .rsvp-fine-print p { font-size: 1rem; } }

.block-rsvp { text-align: center; }

@media screen and (min-width: 48rem) { .block:nth-of-type(even) .block-images { order: 1; }
  .block:nth-of-type(even) .block-content { order: 2; margin-left: 5%; margin-right: 0px; }
  .block .block-content { flex-basis: 55%; }
  .block .block-images { flex-basis: 40%; }
  .block .block-images img { max-width: none; width: 100%; } }

@media screen and (min-width: 76rem) { .block:nth-of-type(even) .block-images { transform: translate(-4rem, 0px); }
  .block:nth-of-type(even) .block-content { margin: 0px; transform: translate(4rem, 0px); }
  .block .block-content { margin-right: 0px; transform: translate(-4rem, 0px); width: 70%; }
  .block .block-images { transform: translate(4rem, 0px); } }

.other-places { margin-top: 2rem; font-size: 1.25rem; text-align: center; }

.hero { position: relative; height: 570px; color: #fff; }

@media screen and (min-width: 48rem) { .hero { height: calc(100vh - 5rem); } }

@media screen and (min-width: 48rem) and (min-height: 500px) { .hero { min-height: 650px; } }

.hero-back { z-index: 0; position: absolute; top: 0px; left: 50%; bottom: 0px; transform: translate(-50%, 0px); background-size: cover; background-position: 8.5% 50%; background-image: url("./dist/images/photos/bridge_cropped.jpg"); width: 100%; max-width: 88rem; opacity: 0; animation-name: fadeIn; animation-duration: 400ms; animation-fill-mode: forwards; animation-timing-function: linear; }

@media screen and (min-width: 375px) { .hero-back { background-position: 6% 50%; } }

@media screen and (min-width: 64rem) { .hero-back { background-position: center center; } }

.hero-back:after { content: ' '; z-index: 1; position: absolute; bottom: 0px; left: 0px; right: 0px; height: 40%; background: linear-gradient(to top, rgba(0, 0, 0, 0.5) 0%, transparent 100%); }

@media screen and (min-width: 48rem) { .hero-back:after { content: ''; display: none; } }

.hero .content { z-index: 2; position: absolute; bottom: 10%; left: 1rem; right: 1rem; text-align: center; opacity: 0; animation-name: fadeInUpHero; animation-delay: 1200ms; animation-duration: 500ms; animation-fill-mode: forwards; animation-timing-function: ease-out; text-shadow: 0px 0px 0.125rem rgba(0, 0, 0, 0.95); }

@media screen and (min-width: 48rem) { .hero .content { left: 3rem; right: 3rem; bottom: 15%; text-shadow: 0px 0px 0.125rem rgba(0, 0, 0, 0.85); } }

.hero h1, .hero h2 { color: #fff; }

.hero h1::selection, .hero h2::selection { background: #313131; }

.hero h1 { margin-bottom: 0.5rem; }

@media screen and (min-width: 64rem) { .hero h1 { font-size: 4.5rem; } }

.hero h2 { margin: 0px; font-size: 1.25rem; }

.hero span { display: block; clear: both; font-size: 0.5rem; text-indent: -9999px; }

@media screen and (min-width: 48rem) { .hero span { display: inline; text-indent: 0px; font-size: inherit; } }

@media (min-width: 64rem) { header[data-scroll="out"] { z-index: 9999; position: fixed; top: 0px; left: 0px; right: 0px; background: #fff; } }

nav { margin: 1.5rem 2rem; font-size: 1.25rem; line-height: 1; text-transform: uppercase; letter-spacing: 1.25px; }

@media screen and (min-width: 64rem) { nav { margin: 0px 2rem; opacity: 0; animation-name: fadeInUpNav; animation-delay: 1700ms; animation-duration: 250ms; animation-fill-mode: forwards; animation-timing-function: ease-out; } }

nav a { display: block; color: #313131; text-decoration: none; transition: all 250ms; }

nav a:hover { transform: translate(0px, -1px); }

nav ul { list-style-type: none; margin: 0px; padding: 0px; text-align: center; }

nav ul li:not(:first-child) { margin-top: 1.5rem; }

@media screen and (min-width: 64rem) { nav ul li { display: inline-block; vertical-align: middle; margin: 1.875rem 0.75rem; font-size: 1rem; }
  nav ul li:not(:first-child) { margin-top: 1.875rem; } }

.wedding-party-group { display: flex; flex-wrap: wrap; justify-content: space-between; align-items: center; }

.wedding-party-group:after { content: ' '; display: block; clear: both; }

.wedding-party-group:not(:first-child) { margin-top: 3%; }

@media screen and (min-width: 48rem) { .wedding-party-group:not(:first-child) { margin-top: 1%; } }

.party-member { position: relative; background-size: cover; background-position: center center; filter: grayscale(100%); overflow: hidden; transition: filter 300ms; flex-basis: calc((100% - 1rem) / 2); margin-left: 1rem; }

.party-member:after { content: ''; display: block; padding-bottom: 100%; }

.party-member:hover { cursor: pointer; filter: grayscale(0%); }

.party-member.filler:hover { cursor: inherit; }

.party-member-inner { display: none; text-align: center; }

.member-popup .party-member-inner { display: block; }

.party-member .cropped { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); min-width: 100%; min-height: 100%; width: auto; height: 100%; max-width: none; max-height: none; }

.party-member .full { display: none; }

.party-member:nth-child(odd) { margin-left: 0px; }

.party-member:nth-child(n+3) { margin-top: 1rem; }

@media screen and (min-width: 48rem) { .party-member { flex-basis: calc((100% - 3rem) / 4); margin-left: 1rem; }
  .party-member:nth-child(odd) { margin-left: 1rem; }
  .party-member:nth-child(4n-3) { margin-left: 0px; }
  .party-member:nth-child(n+3) { margin-top: 0px; }
  .party-member:nth-child(n+5) { margin-top: 1rem; } }

.member-popup { display: none; position: fixed; top: 0px; left: 0px; right: 0px; bottom: 0px; background: rgba(0, 0, 0, 0.75); }

.member-popup .popup-inner { box-sizing: border-box; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); padding: 1rem; width: calc(100% - 2rem); background: #fff; }

@media screen and (min-width: 48rem) { .member-popup .popup-inner { padding: 2rem; width: 38rem; } }

@media screen and (min-width: 64rem) { .member-popup .popup-inner { width: 54rem; } }

@media screen and (min-width: 75rem) { .member-popup .popup-inner { width: 65rem; } }

.member-popup .popup-inner .popup-close { position: absolute; top: 1rem; right: 1rem; font-size: 3rem; font-weight: bold; line-height: 1.75rem; }

.member-popup .popup-inner .popup-close:hover { cursor: pointer; opacity: 0.75; }

.member-popup .popup-inner img { display: block; margin: 0px auto; max-width: 80%; max-height: 200px; }

.member-popup .popup-inner img + .popup-content { margin-top: 1rem; }

.member-popup .popup-inner p { font-size: 1rem; line-height: 1.4; }

.member-popup .popup-inner .member-name { margin-bottom: 1rem; }

@media screen and (min-width: 64rem) { .member-popup .popup-inner img { max-width: 400px; max-height: 500px; }
  .member-popup .popup-inner p { font-size: 1.25rem; } }

body.popup-active { overflow: hidden; }

body.popup-active .member-popup { display: block; }

