@keyframes fadeInUpHero {

  0% {
    opacity: 0;
    transform: translate(0px, 20%);
  }

  to {
    opacity: 1;
    transform: translate(0px, 0px);
  }

}

@keyframes fadeInUpNav {

  0% {
    opacity: 0;
    transform: translate(0px, 100%);
  }

  to {
    opacity: 1;
    transform: translate(0px, 0px);
  }

}

